.myModal {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	display: none;
	background: rgba(0, 0, 0, 0.5);
	z-index: 1;
}

.myModalContent {
	padding: 25px;
	background: white;
	border-radius: 16px;
	min-width: 250px;
}

.myModal.active {
	display: flex;
	justify-content: center;
	align-items: center;
}

@media screen and (max-width: 580px) {
	.myModalContent {
		max-width: 320px;
	}
}
@media screen and (max-width: 340px) {
	.myModalContent {
		max-width: 300px;
	}
}
